@value colorAction from '../colors.css';

.btn {
  color: #512387;
  background: white;

  display: -ms-inline-flexbox;

  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

  height: 31px;
  padding: 0 10px;

  cursor: pointer;
}

.page {
  composes: btn;
  min-width: 30px;
}

.pageDisabled {
  composes: page;
  color: #a7a7a7;
}

.page + .page {
  margin: 0 0 0 5px;
}

.next {
  composes: btn;
}

.pager {
  display: -ms-flexbox;
  display: flex;
  margin: 0 0 32px 0;

  font-size: 14px;
}

.pager > .pages {
    margin: 0 0 0 auto;
  }

.pager > .next {
    margin: 0 0 0 11px;
  }



