@value cardWidth: 297px;
@value cardHeight: 297px;
@value colorAction, cardAttrsColor, cardDescriptionColor, cardBg from '../colors.css';

.card {
  width: 297px;
  height: 297px;
  padding: 17px 18px;

  background: white;
}

.card > .title {
    margin: 0 0 10px 0;
  }

.card > .attrs {
    margin: 0 0 12px 0;
  }

.title {
  color: #512387;

  font-size: 18px;

  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.attrs {
  line-height: 14px;
}

.attr {
  color: #918e8e;
  font-size: 12px;
}

.attr + .attr::before {
  content: '';
  display: inline-block;

  margin: 0 4px 0 8px;
  width: 1px;
  height: 11px;

  background: #918e8e;
  position: relative;
  top: 2px;
}

.descr {
  font-size: 12px;
  color: black;
}



