@value cardWidth from "./components/Card.css";
@value cardsMarginH from "./components/Cards.css";
@value mainBg, footerBg, footerBorder, logoShadow, logoBg from "./colors.css";

html {
}

body {
  background: #dbdbdb;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: sans-serif;
}

.page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;

  min-height: 100vh;
}

.header {
  padding: 23px 0 46px 0;
}

.content {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
}

.pagerWrap {
  margin: auto 0 0 0;
}

.footer {
  height: 84px;
  background: #a7a7a7;

  border: 1px solid #ababab;
  border-left: 0;
  border-right: 0;
}

.logo {
  width: 85px;
  height: 85px;
  border-radius: 43px;

  background: white;
  -webkit-box-shadow: 0 0 5px #b7b7b7;
          box-shadow: 0 0 5px #b7b7b7;
}

.header, .content {
  width: 297px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .header, .content {
    width: 636px
  }
}

@media (min-width: 992px) {
  .header, .content {
    width: 975px
  }
}



